.names{
    font-size: 20px;
    color:rgba(233, 24, 24, 0.932);
}

input{
    height: 30px;
    font-size: 30px;
    width: auto;
    color: slategray;
    border-top: none;
    border-right:none;
    border-left:none;
    border-bottom-color: blue;
}
.sub{
    font-size:10px;
    margin:16px;
    background-color:rgb(121, 118, 129);
    color:white;
    border-color:rgb(121, 118, 129);
    border-radius :13px;
    padding:10px;
}
.contact{
    display: grid;
    grid-template-columns: 200px 200px 200px 200px 2000px 200px;
    grid-template-rows: 70px 70px 70px 70px 70px 70px;
    grid-gap: 20px;
}
.cont_name{
    display:inline-grid;
    background-color: tomato;
    border-radius: 15px;
    font:30px solid;
    padding: 20px;
}
.allinfo{
    border: 2px solid black;
    position: absolute;
    top:400px;
    left: 400px;
    height: 500px;
    width: 900px;
    display: grid;
    grid-template-rows:auto auto auto auto;
    color: #544f4f;
    background-color:#96abab;
    grid-gap: 5px;
    padding: 0px 5px;
}
.dataname{
    font-size:80px ;
    grid-column:1;
    color: #f1f5f8;
    
}
.datanumber{
    grid-column:1/4;
    border-radius: 15px;
    background-color:#f1f5f8;
    border:none;
}
.datacompany{
    border:1px solid  ;
    grid-column:1/4;
    border-radius: 15px;
    background-color:#f1f5f8;
}
.dataemail{
    border:1px solid  ;
    grid-column:1/4;
    border-radius: 15px;
    background-color:#f1f5f8;
}
.close{
    grid-row:1;
    background-color: white;
    color: red;
    height: 25px;
    width: 30px;
    border:none;
}

